//
// Navs Custom
// --------------------------------------------------

.nav-tabs {
  > li, > li:last-child {
    margin-bottom: -1px;
  }
}

.nav-stacked {
  > li + li {
    margin-top: 4px;
  }
}

.nav-custom {
  background: #fff;
  border-radius: 50px;
  overflow: hidden;
  border: 1px solid #d9d9d9;

  .nav-item {
    flex-basis: 100%;
  }

  .nav-link {
    position: relative;
    padding: 7px 18px;
    margin-bottom: 0;
    font-size: 20px;
    color: #d9d9d9;
    font-weight: 900;
    border-radius: 0;

    span {
      display: block;
      font-size: 10px;
      font-weight: 400;
    }
  }

  @include media-breakpoint-up(md) {
    .nav-item {
      flex-basis: auto;

      &:last-child {
        .nav-link::before {
          content: none;
        }
      }
    }
    
    .nav-link {
      &::before {
        content: '';
        position: absolute;
        width: 1px;
        right: 0;
        top: 14px;
        bottom: 14px;
        background-color: #d9d9d9;
        display: block;
        z-index: 1;
      }

      &.active {
        &::before {
          content: none;
        }
      }
    }
  }
}


// Stacked pills
// --------------------------------------------------


@include media-breakpoint-up(lg) {
  .nav-stacked-container {
    .tab-content,
    .nav-stacked {
      display: table-cell;
      vertical-align: top;
    }

    .nav-stacked {
      width: 25%;
    }

    .tab-content {
      width: 73%;
      padding-left: 20px;
    }
  }
}
